@import './var.scss';
.tempalte-pageTab-nav {
  display: flex;
  overflow: hidden;
  .tempalte-pageTab-nav-item {
    position: relative;
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
    height: 60px;
    font-size: 20px;
    font-family: PingFangSC-Semibold, PingFang SC;
    font-weight: 600;
    background: RGBA(40, 40, 61, 1);
    color: rgba(255, 255, 255, 0.5);
    cursor: pointer;
  }
  .tempalte-pageTab-nav-active {
    background: #2f2f41;
    color: rgba(255, 255, 255, 1);
    &::before {
      content: '';
      position: absolute;
      left: -29px;
      top: 0;
      width: 30px;
      height: 100%;
      background: url(../images/ico_tab_before.png) no-repeat;
      background-size: 100% 100%;
    }
    &::after {
      z-index: 2;
      content: '';
      position: absolute;
      right: -29px;
      top: 0;
      width: 30px;
      height: 100%;
      background: url(../images/ico_tab_after.png) no-repeat;
      background-size: 100% 100%;
    }
  }
}

@include mobile-only {
  .tempalte-pageTab-nav {
    .tempalte-pageTab-nav-item {
      height: 44px;
      font-size: 18px;
    }

    .tempalte-pageTab-nav-active {
      &::before {
        left: -22px;
        width: 23px;
      }
      &::after {
        right: -22px;
        width: 23px;
      }
    }
  }
}
