.total-val {
  &-wrap {
    text-align: center;
    padding: 30px 0 40px;
    font-family: Avenir Next;
    color: #fff;
    font-weight: bold;
    font-size: 30px;
    line-height: 30px;
  }
  &-title {
    font-size: 18px;
    line-height: 25px;
    margin-bottom: 20px;
  }
  &-count {
    font-weight: bold;
    font-size: 50px;
    line-height: 50px;
  }
}
.mobile-content {
  .total-val {
    &-title {
      font-size: 16px;
    }
    &-count {
      font-size: 35px;
    }
  }
}
