.v3-loading-box {
  width: 100%;
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  > span {
    display: inline-block;
    width: 50px;
    height: 50px;
    background: url(../../assets/images/loading-2.png) 0 0 no-repeat;
    background-size: 100%;
    animation: sunSwapv3Rotate 1s infinite linear;
  }
}

@keyframes sunSwapv3Rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
