$white: #fff;
$purple: #c1a0ff;

.v3-pool-page {
  .position {
    border-radius: 10px;
    margin-bottom: 10px;
    background: #444454;
    padding: 10px;
    position: relative;
    cursor: pointer;
    &-header {
      display: flex;
      justify-content: space-between;
      &-logo {
        position: relative;
        img {
          width: 20px;
          height: 20px;

          &:nth-child(2) {
            position: absolute;
            left: 14px;
            top: 3px;
          }
        }
      }
      &-info {
        margin-left: 25px;
        &-top {
          font-weight: bold;
          color: $white;
          font-size: 15px;

          span {
            font-size: 12px;
            opacity: 0.3;
            margin-left: 10px;
          }
        }
      }
    }
    &-content {
      display: flex;
      margin-top: 10px;
      > div {
        margin-right: 6px;
      }
      &-title {
        font-size: 12px;
        color: rgba(255, 255, 255, 0.5);
      }
      &-info {
        font-weight: bold;
        color: $white;
        &:nth-child(1) {
          margin-right: 16px;
        }
      }
    }
  }
}
