$border-color:rgba(255,255,255,.3);
$active-bg:linear-gradient(135deg, #9017FF 0%, #5A14E1 100%);
$text-color: #fff;
$desc-color:rgba(255,255,255,.5);
$no-create-color:rgba(255,255,255,.7);

.fee-selector {
  &-item {
    border: 1px solid $border-color;
    color: $text-color;
    border-radius: 10px;
    padding: 10px;
    cursor: pointer;
    height: 100%;
    min-height: 116px;
    display: flex;
    flex-direction: column;
    &-active {
      border: 1px solid transparent;
      background: $active-bg;
      cursor: default;
    }
    &-percent {
      font-size: 20px;
      font-weight: bold;
      color: $text-color;
      line-height: 24px;
      margin-bottom: 6px;
    }
    &-desc {
      font-size: 15px;
      color: $desc-color;
      line-height: 18px;
    }
    &-no-create {
      font-size: 15px;
      color: $no-create-color;
      line-height: 18px;
      font-weight: bold;
      margin-top: auto;
    }
  }
}
.mobile-content {
  .fee-selector-item {
    padding: 5px;
    &-percent {
      font-size: 16px;
    }
    &-desc,&-no-create {
      font-size: 12px;
    }
  }
}