$step-finish-color:#16c378;
$step-process-color:#5915e1;
$step-error-color: #ff8e18;

.v3-add-liquidity-modal {
  .ant-modal-footer {
    border: none;
    text-align: center;
  }
  .ant-modal-content {
    border-radius: 20px;
  }
}
.v3-step-modal-steps {
  background: rgba(0, 0, 0, 0.03);
  padding: 20px;
  border-radius: 5px;
  .v3-steps-list .ant-steps-item-finish {
    .ant-steps-item-icon {
      border-color: $step-finish-color;
      .ant-steps-icon {
        color: $step-finish-color;
      }
    }
    .ant-steps-item-container .ant-steps-item-title {
      color: $step-finish-color;
    }
    .ant-steps-item-container .ant-steps-item-tail::after {
      background-color: $step-finish-color
    }
  }
  .v3-steps-list .ant-steps-item-process {
    .ant-steps-item-icon {
      background: $step-process-color;
      border-color: $step-process-color
    }
    .ant-steps-item-title, .ant-steps-item-description {
      color: $step-process-color
    }
    .ant-steps-item-tail:after {
      background-color: $step-process-color;
    }
  }
  .v3-steps-list .ant-steps-item-error {
    .ant-steps-item-icon {
      border-color: $step-error-color;
    }
    .ant-steps-item-icon > .ant-steps-icon {
      color: $step-error-color;
    }
    .ant-steps-item-title, .ant-steps-item-description {
      color: $step-error-color;
    }
  }
}
.v3-trans-m {
  &-title-wrap {
    line-height: 2em;
    text-align: center;
  }
  &-title {
    font-size: 15px;
    font-weight: 500;
    color: #1a1a1a;
  }
  &-amount {
    margin-right: 5px;
    font-size: 20px;
    color: #333;
  }
  &-token {
    font-size: 12px;
    color: #999;
  }
  &-connect {
    margin: 0 4px;
    font-weight: bold;
    font-size: 15px;
    color: #5915e1;
  }
  &-tip {
    margin: 20px 0 12px;
    font-size: 12px;
    color: #666;
  }
}
.v3-trans-btn-disabled.v3-purple-btn.ant-btn[disabled] {
  background: #dbdbdb;
  color: #fff;
}