$blue: #5915e1;
$black: #333333, #666666, #999999;
$gray: #fafafa, #dbdbdb;
$orange: #ff8e18;
$green: #16c378;
$red: #e85d54;
$hoverBlue: #4711b4;
$hoverOrgange: #cc7113;
$superPurple: #5816e0;
$font333: #333;
$font666: #666;
$font999: #999;
$bgColor: #140736;

button {
  &.single {
    background: #5915e1;
    border-radius: 10px;
    color: #ffffff;
    cursor: pointer;
    width: 280px;
    height: 50px;
    line-height: 50px;
    display: block;
    margin: 0 auto;
    font-size: 14px;
    font-family: PingFangSC-Regular;

    &:hover {
      background: $hoverBlue;
    }

    &.gray {
      &:hover {
        background: nth($gray, 2);
      }

      background: nth($gray, 2);
    }

    &.half {
      width: 49%;
      max-width: 260px;
      margin-left: 2%;

      &:first-child {
        margin-left: 0;
      }
    }

    &.trisection {
      width: 32%;
      max-width: 160px;
      margin-left: 25px;

      &:first-child {
        margin-left: 0;
      }
    }

    &.blue {
      background: $superPurple;
      color: #fff;
    }

    &.orange {
      background: $orange;
      color: #fff;
    }
  }
}
