.v3-range-selector {
  display: flex;
  justify-content: space-between;
}
.v3-step-counter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  //width: 220px;
  &-wrap {
    background: #444454;
    border-radius: 10px;
    padding: 10px;
    &-active {
      border: 1px solid #C1A0FF;
    }
  }
  &-title {
    font-size: 12px;
    font-weight: bold;
    color: rgba(255,255,255,.5);
    line-height: 14px;
    margin-bottom: 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
#root .v3-step-counter .rate-input-0 {
  border: none;
  padding-top: 0;
  padding-bottom: 0;
  background: transparent;
  color: #fff;
  height: 29px;
  font-size: 24px;
  font-weight: bold;
  line-height: 29px;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.base-btn {
  flex-shrink: 0;
  display: inline-block;
  width: 30px;
  height: 30px;
  border-radius: 4px;
  border: 1px solid rgba(255,255,255,.3);
  color: #fff;
  text-align: center;
  line-height: 30px;
  cursor: pointer;
  background: transparent;
  &-disable {
    cursor: no-drop;
  }
}