@keyframes Gradient {
  0% {
    background-position: 0% 50%
  }
  50% {
    background-position: 100% 50%
  }
  100% {
    background-position: 0% 50%
  }
}

.n-token-modal-title-icon {
  margin-left: 10px;
  color: rgba(255, 255, 255, .5);
}

.sun-mobile-token-modal, .token-select-modal {
  font-size: 14px;
  color: #FFFFFF;
  .ant-drawer-content-wrapper {
    border-radius: 20px 20px 0 0;
    overflow: hidden;
  }
  .ant-drawer-content, .ant-drawer-header {
    background-color: rgba(47, 47, 65, 1)
  }
  .ant-drawer-header {
    border: none;
    padding: 16px 15px 0;
  }
  .ant-drawer-close {
    color: #fff;
  }
  .ant-drawer-body {
    padding: 0;
  }

  .n-token-modal-title {
    color: #fff;
    text-align: left;
  }

  .n-select-search {
    width: 100%;
    height: 29px;
    background: #444454;
    border-radius: 15px;
  }

  .n-list-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px 15px 12px;
  }

  .icon-sort {
    width: 17px;
    height: 15px;
    background: url(../images/sort_gray.svg) no-repeat;
    background-size: 100%;
    cursor: pointer;

    &.icon-sort-active {
      background: url(../images/sort_active.svg) no-repeat;
      background-size: 100%;
    }
  }

  .n-list {
    height: 285px;
    overflow-y: auto;
  }
  .n-list-loading {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 460px;
    background: #2F2F41;
    z-index: 1;
    &-item {
      display: flex;
      justify-content: space-between;
      margin-top: 20px;
    }
    &-right {
      width: 60px;
      height: 18px;
      background: linear-gradient(149deg, rgba(255,255,255,0.05) 0%, rgba(255,255,255,0.15) 47%, rgba(255,255,255,0.05) 100%);
      border-radius: 9px;
      background-size: 400% 400%;
      animation: Gradient 1.5s ease infinite;
    }
    &-top {
      width: 120px;
      height: 16px;
      background: linear-gradient(149deg, rgba(255,255,255,0.05) 0%, rgba(255,255,255,0.15) 47%, rgba(255,255,255,0.05) 100%);
      border-radius: 8px;
      margin-bottom: 6px;
      background-size: 400% 400%;
      animation: Gradient 1.5s ease infinite;
    }
    &-bottom {
      width: 200px;
      height: 8px;
      background: linear-gradient(149deg, rgba(255,255,255,0.05) 0%, rgba(255,255,255,0.15) 47%, rgba(255,255,255,0.05) 100%);
      border-radius: 5px;
      background-size: 400% 400%;
      animation: Gradient 1.5s ease infinite;
    }
  }

  .n-list-item {
    display: flex;
    align-items: center;
    padding: 12px 15px;
    cursor: pointer;

    &:not(&-selected):hover {
      background: #28283D;
    }
    &:first-child {
      padding-top: 0;
    }

    &-selected {
      opacity: 0.3;
      //cursor: not-allowed;
    }
  }

  .n-token-icon {
    width: 40px;
    height: 40px;
    margin-right: 10px;
    border-radius: 50%;
  }

  .n-item-content {
    flex: 1;
  }

  .n-item-title {
    font-size: 14px;
    font-weight: 600;
    color: #FFFFFF;
    line-height: 20px;
    margin-bottom: 3px;
  }

  .n-item-address {
    font-size: 12px;
    font-weight: 400;
    color: rgba(255, 255, 255, .5);
    line-height: 17px;
  }

  .n-list-switch-wrap {
    padding: 20px 15px 30px;
  }

  .n-list-switch {
    display: flex;
    height: 50px;
    background: #444454;
    border-radius: 5px;
    font-size: 12px;
    font-weight: 400;
    color: #FFFFFF;
    padding: 0 16px;
    align-items: center;

    &-icon {
      width: 30px;
      height: 30px;
      margin-right: 12px;
    }

    &-name {
      flex: 1;
    }

    &-btn {
      width: 52px;
      height: 29px;
      background: #5A14E1;
      border-radius: 15px;
      cursor: pointer;
    }
  }

  .n-origin-list-item {
    display: flex;
    align-items: center;
    padding: 10px 0;

    &-left {
      flex: 1;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &-img-text {
      display: flex;
      align-items: center;
    }

    &-logo {
      width: 40px;
      height: 40px;
      margin-right: 10px;
    }

    &-drop {
      width: 15px;
      height: 12px;
      background: url(../images/Triangle.svg) no-repeat;
      background-size: 100%;
      min-width: 15px;
      margin-right: 20px;
      &.ant-dropdown-open {
        transform: rotate(180deg);
      }
    }
    &-source {
      font-size: 12px;
      font-weight: 400;
      color: rgba(255, 255, 255, .5);
      line-height: 17px;
      margin-top: 3px;
    }
    &-name {
      font-size: 14px;
      font-weight: 600;
      color: #FFFFFF;
      line-height: 20px;
    }
  }

  .n-origin-list {
    overflow-y: auto;
    height: 390px;
  }

  .n-list-more, .list-add-footer {
    a {
      color: #9a8aff;
    }
  }

  .n-origin-list-more {
    margin-top: 40px;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 18px;
    text-decoration-line: underline;
    a {
      color: #9a8aff;
    }
  }

  .list-add-btn {
    margin-top: 20px;
    background: #efefef;
    border-radius: 10px;
    padding: 9px 18px;

    .list-add-input-line {
      input {
        background: #efefef;
        border: none;
        padding: 5px;
        border-radius: 5px;
        font-family: Avenir Next;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 19px;
        color: #666666;
        box-shadow: none;
      }

      .list-add-right {
        button {
          background: #5915e1;
          box-shadow: 0px 10px 40px rgba(51, 101, 249, 0.2);
          border-radius: 10px;
          min-width: 97px;
          font-family: PingFang SC;
          font-style: normal;
          font-weight: normal;
          font-size: 14px;
          line-height: 20px;
          padding: 9px 0;
          color: #ffffff;
          cursor: pointer;
        }
      }
    }
  }

  .list-add-footer {
    margin: 12px 0;
  }

  .ant-input-affix-wrapper:focus, .ant-input-affix-wrapper:hover {
    border: none;
    box-shadow: none;
  }

  .n-hot-coin {
    margin-top: 20px;
    padding: 0 15px;
  }

  .n-hot-title {
    font-size: 14px;
    font-weight: 600;
    color: #FFFFFF;
    line-height: 20px;
  }

  .n-hot-list {
    display: flex;
    flex-wrap: wrap;
  }

  .n-hot-item {
    padding: 5px 8px;
    background: #444454;
    border-radius: 5px;
    font-size: 12px;
    font-weight: 600;
    color: #FFFFFF;
    display: flex;
    align-items: center;
    cursor: pointer;
    margin: 9px 5px 0 0;
    &:last-child {
      margin-right: 0;
    }
    &-selected, &:hover, &:focus {
      opacity: 0.5;
    }
  }

  .n-hot-icon {
    width: 15px;
    height: 15px;
    margin-right: 7px;
  }

  .n-add-title {
    color: #fff;
  }

  .loading-hot {
    &-list {
      display: flex;
      margin-top: 10px;
    }
    &-item {
      width: 70px;
      height: 25px;
      background: linear-gradient(149deg, rgba(255,255,255,0.05) 0%, rgba(255,255,255,0.15) 47%, rgba(255,255,255,0.05) 100%);
      border-radius: 5px;
      background-size: 400% 400%;
      animation: Gradient 1.5s ease infinite;
      margin-right: 20px;
      &:last-child {
        margin-right: 0;
      }
    }
  }
}

.token-select-modal {
  box-shadow: 0 30px 300px 0 rgba(42, 42, 62, 1);
  padding: 0;
  border-radius: 20px;

  .ant-modal-header {
    background: #2F2F41;
    color: #fff;
  }

  .ant-modal-content {
    background: #2F2F41;
    box-shadow: 0 30px 300px 0 rgba(42, 42, 62, 1);
    border-radius: 20px;
  }
  .sun-search-input-wrap {
    margin: 0 15px;
  }
}

.token-select-list-modal.dark-modal.token-select-modal {
  .ant-modal-body {
    padding: 0 15px 30px 15px !important;
  }
}


.list-add-errTip {
  margin-top: 2px;
  text-align: left;
  padding-left: 20px;
  display: flex;
  align-items: center;

  .icon {
    color: #fff;
    background: #ff8e18;
    display: inline-block;
    width: 14px;
    height: 14px;
    border-radius: 14px;
    vertical-align: top;
    margin-right: 3px;
    text-align: center;
    line-height: 14px;
    font-size: 12px;
  }

  .text {
    color: #ff8e18;
    font-family: PingFang SC;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 17px;
  }
}
.sun-mobile-token-modal {
  .home-select-url-input-wrap {
    padding: 0 15px;
  }
  .sun-search-input-wrap {
    margin: 20px 15px;
  }
  .n-origin-list {
    margin: 0 15px;
    height: 430px;
  }
  .ant-drawer-header {
    padding-bottom: 15px;
  }
  .list-add-btn {
    margin: 12px 15px 0;
  }
  .list-add-footer {
    margin: 12px 15px 20px;
  }
}

.home-select-url {
  &-links {
    display: flex;
    a, a:visited, a:link {
      font-size: 12px;
      font-weight: 600;
      color: #C1A0FF;
      line-height: 17px;
    }
  }
  &-more {
    margin-right: 20px;
  }
  &-input {
    height: 35px;
    background: #444454;
    border-radius: 5px;
    border: none;
    padding: 9px 10px;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 19px;
    color: #fff;
    box-shadow: none;
    flex: 1;
  }
  &-btn {
    width: 68px;
    height: 29px;
    background: #5A14E1;
    border-radius: 4px;
    font-size: 12px;
    font-weight: 600;
    color: #FFFFFF;
    line-height: 17px;
    margin-left: 12px;
    cursor: pointer;
  }
  &-bottom {
    display: flex;
    justify-content: space-between;
  }
  &-input-wrap {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  &-links, &-bottom {
    padding: 0 15px;
  }
}

.home-select-url-drop {
  .ant-dropdown-menu.ant-dropdown-menu-root {
    background: #444454;
    font-size: 12px;
    font-weight: 600;
    color: rgba(255, 255, 255, .5);
    line-height: 17px;
  }
  .ant-dropdown-menu-item {
    color: rgba(193, 160, 255, 1);
    font-size: 12px;
    line-height: 17px;
    text-align: left;
    margin: 0;
    padding: 5px;
    &:hover {
      background: transparent;
    }
    a {
      color: rgba(193, 160, 255, 1);
      margin: 0;
      padding: 0;
      font-size: 12px;
    }
  }
  .ant-dropdown-menu {
    box-shadow: 0px 2px 30px rgba(42, 42, 62);
  }
}