.home-v3 {
  .tempalte-pageTab-nav {
    border-radius: 30px 30px 0 0;
  }
  .panel-header {
    display: flex;
    align-items: center;
    background-color: #2f2f41;
    padding: 20px 30px 25px;
  }
  .panel-header-item {
    text-align: center;
    &:first-child {
      margin: 0 50px 0 0;
    }
  }
  .panel-header-item {
    a,
    a:link,
    a:visited {
      display: inline-block;
      font-size: 20px;
      color: rgba(255, 255, 255, 0.5);
      font-weight: 500;
      font-family: AvenirNext-DemiBold;
      padding: 5px 0;
    }
  }
  .panel-header-item.panel-header-item-active {
    a {
      color: #fff;
      position: relative;
      &:after {
        content: "";
        position: absolute;
        left: 50%;
        bottom: 0;
        width: 30px;
        height: 4px;
        background: linear-gradient(135deg,#9017ff,#5a14e1);
        border-radius: 2px;
        transform: translateX(-50%);
      }
    }
  }
  .page-panel-content {
    position: relative;
    padding: 0px 30px 25px;
    background: #2f2f41 url(../../assets/images/v3-bg.svg) right bottom no-repeat;
    background-size: 240px auto;
    border-radius: 0 0 30px 30px;
    min-height: 240px;
    // &::before {
    //   content: 'V3';
    //   color: rgba(255, 142, 24, 1);
    //   background: rgba(255, 142, 24, 0.1);
    //   display: block;
    //   width: 90px;
    //   height: 40px;
    //   font-size: 18px;
    //   line-height: 40px;
    //   text-align: center;
    //   position: absolute;
    //   top: 0;
    //   left: 0;
    //   z-index: 3;
    //   border-radius: 30px 0 30px 0;
    //   font-family: Avenir Next;
    //   font-weight: 600;
    // }
  }
}
.mobile-content {
  .panel-header-item {
    a,
    a:link,
    a:visited {
      font-size: 16px;
    }
  }
}