$white: #fff;

.v3-pool-page {
  .pool-header {
    display: block;
    width: 100%;
    text-align: center;
    padding: 20px 0;
    background: #6210ff;
    border-radius: 10px;
    font-size: 20px;
    font-weight: 500;
    color: $white;
    &:visited {
      color: $white;
    }
  }
  > .flex {
    margin: 28px 0 14px;
  }
  .pool-tilte {
    font-size: 20px;
    font-weight: bold;
    color: $white;
    line-height: 24px;
  }
  .pool-setting {
    font-size: 12px;
    font-weight: bold;
    color: #c1a0ff;
    cursor: pointer;
  }

  .v3-ques-png {
    width: 16px;
    height: 16px;
    margin-left: 6px;
    cursor: pointer;
  }

  .err-list {
    color: #bbb;
    // border: 1px solid #222;
    padding: 50px 0;
    text-align: center;
  }

  .wallet-link {
    display: inline-flex;
    align-items: center;
    padding: 12px 30px;
    color: #5915e1 !important;
    background: #c3a6fa;
    cursor: pointer;
    border-radius: 10px;

    > span {
      line-height: 16px;
      margin-left: 8px;
      margin-right: 38px;
      text-align: left;
    }

    > img {
      &:first-child {
        width: 17px;
        height: 17px;
      }

      &:last-child {
        width: 14px;
        height: 8px;
      }
    }
  }
  .sun-swap-empty-text {
    margin-top: 16px;
  }
}
