.v3-add-page {
  .token-pairs {
    display: flex;
    align-items: center;
    font-size: 15px;
    margin-top: 15px;
    .token-pairs-label {
      opacity: 0.5;
    }
    > div {
      display: flex;
      align-items: center;
      color: #fff;

      .trx-img {
        width: 15px;
        height: 15px;
        margin-left: 10px;
        margin-right: 2px;
      }

      .trx {
        font-weight: bold;
      }

      .typo-text-link {
        display: flex;
        align-items: center;
        margin-left: 10px;

        > img {
          width: 15px;
          height: 15px;
          margin-right: 2px;
        }

        > span {
          font-weight: bold;
          color: #c1a0ff;
          position: relative;
          top: 1px;
        }
      }
    }
  }
}
