$superPurple: #5915e1;
$black: #333333, #666666, #999999;
$db: #dbdbdb;
$orange: #ff8e18;
$green: #16c378;
$hoverOrgange: #cc7113;

.home-v3 {
  .red {
    color: #f00;
  }

  .price-text.mini-red {
    color: #f00;
  }

  .pool-content {
    // background: #fff;
    background: transparent;

    .base-list {
      padding: 0px;

      .item {
        padding: 0;
        background: #fafafa;
        margin-top: 20px;
        border-radius: 20px;

        &:first-child {
          margin-top: 0;
        }

        .flex {
          align-items: center;

          .inline-flex {
            align-items: center;
          }

          .logo {
            width: 25px;
            height: 25px;
            border: 1px solid #dbdbdb;
            border-radius: 25px;

            &:nth-child(2) {
              margin-left: -5px;
            }
          }

          .name {
            margin-left: 10px;
          }

          .symbol {
            margin: 0px;
          }

          &.line-name {
            height: 80px;
            background: #fff;
            padding: 20px;
            box-shadow: 0 10px 40px 0 rgba(21, 55, 156, 0.06);
            border-radius: 20px;

            .name {
              font-family: AvenirNext-Medium;
              font-size: 20px;
              color: #333333;
            }

            .link {
              margin-left: 10px;
              font-family: AvenirNext-Medium;
              font-size: 12px;
              display: block;
            }

            .actions {
              .link {
                font-size: 14px;
                margin: 0;
                padding: 0 20px;
                height: 27px;
                line-height: 27px;

                &:first-child {
                  border-right: 1px solid #d8d8d8;
                }

                &:last-child {
                  padding-right: 0;
                }

                &.disabled {
                  color: #ccc;
                  cursor: not-allowed;
                }
              }

              .gray {
                border-right: 1px solid #d8d8d8;
                color: #dbdbdb;
                cursor: default;
              }

              .migrate {
                background: rgba(88, 22, 224, 0.1);
                border-radius: 13px;
                text-align: center;
                border-right: none !important;

                &:hover {
                  background: #5915e1;
                  color: #fff;
                }
              }
            }
          }
        }

        .line-value {
          padding: 20px;

          .flex {
            p {
              margin-bottom: 6px;
            }

            p:nth-of-type(1) {
              font-family: AvenirNext-Regular;
              font-size: 14px;
              color: #333333;
              height: 19px;
              line-height: 19px;
              word-break: break-word;
              margin-right: 10px;
            }

            p:nth-of-type(2) {
              flex: 1;
              text-align: right;
            }
          }
        }
      }

      a {
        font-size: 14px;
        color: #5915e1;
      }
    }
  }

  .action-content.v3-remove-page {
    header {
      margin-bottom: 20px;

      .pool-add-tool {
        width: 100%;

        .ques {
          margin-right: 0px;
        }
      }

      .add-title {
        display: inline-block;
        vertical-align: middle;
        font-family: AvenirNext-Medium;
        font-size: 16px;
        color: #fff;
        opacity: 0.7;
        > a {
          color: #fff;
        }
      }

      .return {
        cursor: pointer;
        margin-right: 8px;

        img {
          width: 15px;
        }
      }
    }

    .pool-header {
      font-size: 20px;
      font-weight: bold;
      color: #ffffff;
      line-height: 24px;
      opacity: 0.7;
      display: flex;
      align-items: center;
      .pool-link {
        margin: 0 30px 0 10px;
        img {
          width: 14px;
          height: 14px;
        }
      }
    }

    .first-pool-copy {
      margin: 30px 0;
    }

    .token-sec {
      box-shadow: 0 10px 40px 0 rgba(21, 55, 156, 0.06);
      border-radius: 20px;
      justify-content: space-between;
      padding: 20px;
      font-family: AvenirNext-Regular;
      font-size: 14px;
      color: #666666;
      background: #fff;

      .input {
        width: 60%;

        input {
          border: none !important;
          width: 86%;
          padding: 0 10px;
          height: 40px;
          line-height: 40px;
          margin-left: -10px;
          font-family: AvenirNext-DemiBold;
          font-size: 20px;
          color: #333333;

          &:hover {
            border: none;
            box-shadow: none;
          }
        }

        .link {
          background: #ff8e18;
          border-radius: 9px;
          height: 20px;
          padding: 0 13px;
          line-height: 20px;
          color: #fff;
          font-family: AvenirNext-Medium;
          margin-top: 5px;
          font-size: 12px;
          display: inline-block;
        }
      }

      .select {
        text-align: right;
        //   width: 35%;
        //   padding-left: 20px;
        //   border-left: 1px solid #ccc;
      }

      .dragDown {
        display: inline-flex;
        width: auto;
        padding-right: 25px;
        margin: 10px auto 0;
        // background: url(../images/arr.svg) right no-repeat;
        cursor: pointer;

        &.no-bg {
          background: none;
          cursor: auto;
        }

        &.default {
          // margin-top: 30px;

          span {
            margin: 0;
          }
        }

        img {
          width: 26px;
          height: 26px;
        }

        span {
          vertical-align: middle;
          display: inline-block;
          margin: 0 0 0 5px;
        }
      }
    }

    .plusBtn {
      text-align: left;
      margin: 14.5px 0;
      padding: 0 20px;

      img {
        width: 18px;
        height: 18px;
      }
    }

    .action-ui-box {
      // margin: 30px 0 30px;
      background: transparent;
      // background: #ffffff;
      // box-shadow: 0 10px 40px 0 rgba(21, 55, 156, 0.06);
      // border-radius: 20px;
      // padding: 20px;

      .action-info-text {
        font-size: 20px;
        color: #ffffff;
        opacity: 0.7;

        .link {
          color: #c1a0ff;
        }
      }
    }

    .info-view {
      justify-content: flex-end;
      margin-top: 15px;

      a {
        color: #5915e1;
        font-size: 14px;
      }
    }

    .action-info {
      // background: #ffffff;
      // border-radius: 10px;
      .info-title {
        margin-top: 20px;
        padding: 10px 10px;
        font-family: AvenirNext-Medium;
        font-size: 14px;
        color: #fff;

        > div {
          width: 33%;

          &:last-child {
            display: initial;
          }
        }

        &.pd0 {
          padding: 0;
          margin-top: 0;
        }

        img {
          width: 20px;
          height: 20px;
          margin-right: 3px;

          &:nth-child(2) {
            margin-left: -3px;
          }
        }

        span {
          vertical-align: middle;
          opacity: 0.7;

          &:nth-child(3) {
            margin-left: 7px;
          }
        }

        a {
          color: #c1a0ff;
        }
      }

      .info-detail {
        margin-top: 14px;
        padding: 20px;
        background: #fafafa;
        border-radius: 20px;
        justify-content: space-between;
        font-family: AvenirNext-Regular;
        font-size: 14px;
        color: #666666;

        div {
          width: 33%;
          display: inline-block;

          &.d8line {
            width: 1px;
            height: 27px;
            background: #d8d8d8;
          }

          &:nth-child(3) {
            text-align: center;
          }

          &:nth-child(5) {
            text-align: right;
          }

          &:last-child {
            border-right: none;
          }

          p {
            font-family: AvenirNext-Regular;
            font-size: 14px;
            color: #666666;

            .unit {
              margin: 0 5px;
            }

            &:nth-child(2) {
              margin-top: 5px;
            }
          }
        }
      }

      .v3-percent {
        margin-top: 20px;

        .num {
          font-size: 48px;
          font-family: Helvetica-Bold, Helvetica;
          font-weight: bold;
          color: #ffffff;
          line-height: 58px;
          margin-bottom: 20px;
        }
      }

      .v3-percent-key {
        display: flex;
        margin-top: 20px;
        justify-content: space-between;

        > button {
          border-radius: 4px;
          border: 1px solid #c1a0ff;
          background: transparent;
          font-size: 15px;
          font-family: Helvetica-Bold, Helvetica;
          font-weight: bold;
          color: #c1a0ff;
          margin-right: 20px;
          width: 100%;

          &:hover {
            background: #5a14e1;
            border-color: #5a14e1;
            color: #fff;
          }

          &:last-child {
            margin-right: 0;
          }
        }

        .percent-btn-active {
          width: 100%;
        }
      }

      .sun-swap-normal-3 {
        background: #444454;
        border-radius: 10px;

        &:last-child {
          display: inline-block;
        }
      }
    }

    .token-pairs {
      display: flex;
      margin: 20px 0;
      .sun-swap-normal-1 {
        color: #ffffff;
        opacity: 0.5;
      }
      .typo-text-link {
        margin-left: 10px;
        > img {
          width: 15px;
          height: 15px;
          margin-right: 2px;
        }
      }
      // align-items: center;
      > div {
        display: flex;
        align-items: center;
        color: #fff;

        .typo-text-link {
          display: flex;
          align-items: center;

          > span {
            position: relative;
            top: 1px;
          }
        }
      }
    }

    .action-btns {
      margin-top: 25px;

      button {
        margin-top: 15px;
      }
    }

    .convert {
      margin: 40px 0 0;
      justify-content: space-between;

      img {
        width: 16px;
        height: 16px;
      }

      span {
        vertical-align: middle;
        margin-left: 5px;
        color: #ffffff;
        opacity: 0.7;
      }

      div {
        text-align: center;
        color: #ffffff;
      }

      .token1 div:first-child {
        text-align: left;
        margin-bottom: 10px;
      }

      .token2 div:first-child {
        text-align: right;
        margin-bottom: 10px;
      }

      .convert-info {
        width: 50%;
        color: #333;
        opacity: 0.5;

        div {
          span {
            margin-left: 0;
          }

          &:nth-child(1) {
            border-bottom: 2px solid rgba($color: #ffffff, $alpha: 0.5);

            .hook {
              right: 0;
              bottom: 0;
              width: 10px;
              height: 10px;
              border-right: 2px solid rgba($color: #ffffff, $alpha: 0.5);
              -webkit-transform: rotate(-45deg);
              -moz-transform: rotate(-45deg);
              -o-transform: rotate(-45deg);
              transform: rotate(-45deg);
              /*  */
              -webkit-transform-origin: right bottom;
              -moz-transform-origin: right bottom;
              -o-transform-origin: right bottom;
            }
          }

          &:nth-child(2) {
            border-top: 2px solid rgba($color: #ffffff, $alpha: 0.5);
            margin-top: 7px;

            .hook {
              left: 0;
              top: 0;
              width: 10px;
              height: 10px;
              border-left: 2px solid rgba($color: #ffffff, $alpha: 0.5);
              -webkit-transform: rotate(-45deg);
              -moz-transform: rotate(-45deg);
              -o-transform: rotate(-45deg);
              transform: rotate(-45deg);
              /*  */
              -webkit-transform-origin: left top;
              -moz-transform-origin: left top;
              -o-transform-origin: left top;
            }
          }
        }

        p:nth-child(1) {
          border-bottom: 2px solid rgba(255, 255, 255, 0.3);
        }

        p:nth-child(2) {
          border-top: 2px solid rgba(255, 255, 255, 0.3);
        }
      }
    }
  }

  .main-content #swap-tab .v3-remove-page button.schedule-btn {
    border-radius: 4px;
    border: 1px solid #c1a0ff;
    background: transparent;
    color: #c1a0ff;
    font-weight: bold;
    font-size: 15px;
    font-family: Helvetica-Bold, Helvetica;
    margin-right: 0;

    &:hover {
      background: #5a14e1;
      border: none;
      color: #fff;
    }
  }

  .ant-slider {
    margin: 10px 0;
  }

  .ant-slider.super-slider {
    .ant-slider-rail {
      background: rgba(222, 205, 255, 0.5);
      height: 10px;
      border-radius: 10px;
    }

    .ant-slider-track {
      background: #5a14e1;
      height: 10px;
      border-radius: 10px 0 0 10px;
    }

    .ant-slider-handle {
      width: 20px;
      height: 20px;
      border: 3px solid #fff;
      box-shadow: 0 2px 20px 0 rgba(51, 44, 192, 0.2);
      cursor: pointer;
      border-radius: 50%;
      background: rgba(90, 20, 225, 1);
    }

    .ant-slider-step {
      height: 10px;
    }

    &:hover {
      .ant-slider-rail {
        background: rgba($color: #000000, $alpha: 0.05);
        height: 10px;
        border-radius: 10px;
      }

      .ant-slider-track {
        background: #5915e1;
        height: 10px;
        border-radius: 10px 0 0 10px;
      }

      .ant-slider-handle {
        width: 20px;
        height: 20px;
        border: 3px solid #fff;
        box-shadow: 0 2px 20px 0 rgba(51, 44, 192, 0.2);
        cursor: pointer;
        border-radius: 50%;
        background: rgba(90, 20, 225, 1);
      }

      .ant-slider-step {
        height: 10px;
      }
    }
  }
  .ant-slider.super-slider:hover .ant-slider-rail {
    background-color: rgba(222, 205, 255, 0.5);
  }

  .input-max {
    align-items: center;
    margin-top: 4px;

    .link {
      background: $orange;
      border-radius: 9px;
      height: 20px;
      width: 42px;
      text-align: center;
      line-height: 20px;
      color: #fff !important;
      font-family: AvenirNext-Medium;
      margin-top: 8px;
      font-size: 12px;
      margin-right: 20px;

      &:hover {
        background: $hoverOrgange !important;
        color: #fff;
      }
    }

    display: flex;
  }

  .dragDown span {
    font-family: AvenirNext-Medium;
    font-size: 20px;
    color: #333;
  }

  .rate-title {
    float: none !important;
  }
}

@media only screen and (min-device-width: 375px) and (max-device-height: 667px) and (-webkit-device-pixel-ratio: 2) {
  .pool-content .base-list .item .flex.line-name .name {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    word-break: break-all;
    width: 130px;
    margin-right: -20px;
    margin-bottom: -6px;
    display: inline-block;
  }
}

@media only screen and (min-device-width: 375px) and (max-device-height: 812px) and (-webkit-device-pixel-ratio: 3) {
  .pool-content .base-list .item .flex.line-name .name {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    word-break: break-all;
    width: 130px;
    margin-right: -20px;
    margin-bottom: -6px;
    display: inline-block;
  }
}

@media only screen and (min-device-width: 414px) and (max-device-height: 736px) and (-webkit-device-pixel-ratio: 3) {
  .pool-content .base-list .item .flex.line-name .name {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    word-break: break-all;
    width: 160px;
    margin-right: -20px;
    margin-bottom: -6px;
    display: inline-block;
  }
}

@media screen and (max-width: 1000px) {
  .input-max {
    justify-content: flex-end;

    .link {
      // padding: 0 7px;
      width: 42px;
      text-align: center;
      padding: 0;
      margin-right: 10px;
    }
  }

  .action-content .token-sec .dragDown {
    padding-right: 12px;

    span {
      font-size: 18px;
    }
  }

  .token-sec-input {
    margin-top: 10px;
  }

  body #root .swap-and-pool .main-content #swap-tab ::placeholder,
  html #root .swap-and-pool .main-content #swap-tab ::placeholder {
    font-size: 14px;
  }

  .action-content .token-sec {
    padding: 10px 20px 8px;
  }

  .action-content .token-sec .input input {
    font-size: 14px;
  }

  .ant-modal.select-modal .item > div:nth-child(2),
  .ant-modal.select-modal .item > div:nth-child(3) {
    width: auto;
  }
}

@media screen and (max-width: 750px) {
  .action-content .plusBtn img {
    width: 20px;
    height: 20px;
  }
  .action-content .first-pool-copy {
    padding: 10px 20px;
    background: hsla(0, 0%, 98%, 0.9);
    border-radius: 8px;
  }
}
@media screen and (max-width: 500px) {
  .actions-new {
    display: block !important;

    .link {
      height: 16px !important;
      line-height: 16px !important;
      padding: 0 5px !important;
      font-size: 12px !important;
    }

    > .link {
      height: 27px !important;
      line-height: 27px !important;

      &:first-child {
        border: none !important;
      }
    }
  }

  .pool-content .base-list .item .flex.line-name {
    padding: 10px;
  }

  html .ant-modal.pool-modal .ant-modal-content {
    padding: 30px !important;
  }

  .rate-title {
    display: block;
  }
}

.v3-remove-modal {
  .ant-modal-content {
    padding: 25px 20px;
    border-radius: 20px;
  }
  .ant-modal-body {
    padding: 0;
    text-align: center;
  }
  .title {
    margin: 40px 0;
    font-weight: 500;
    color: #333;
    font-family: PingFangSC-Regular;

    span {
      margin-right: 3px;

      &:last-child {
        margin-right: 0;
      }
    }

    &.big {
      margin: 0 0 5px;
      font-size: 20px;
    }

    &.normal {
      font-size: 15px;
      margin: 0 0 12.5px;
    }

    &.green {
      font-family: PingFangSC-Medium;
      font-size: 25px;
      color: #16c378;
      margin: 30px 0 15px;
    }
  }
  .title.big {
    font-size: 15px;
    font-family: Helvetica;
    color: #333333;
    line-height: 18px;
    text-align: center;
  }
  .amount {
    margin: 20px 0 20px;
    // border-bottom: 1px solid hsla(0, 0%, 84.7%, 0.5);
    font-size: 20px;
    font-family: Helvetica-Bold, Helvetica;
    font-weight: bold;
    color: #333333;
    line-height: 24px;
    .add-token img {
      width: 15px;
      height: 15px;
    }
  }
  .plus {
    font-size: 20px;
    color: #333;
    margin: 0 4px;
    opacity: 0.5;
  }

  .price {
    margin-bottom: 30px;
    .param {
      justify-content: space-between;
    }
    .price-value {
      > img {
        width: 30px;
        height: 30px;
        &:nth-child(2) {
          margin-left: -6px;
        }
      }
      > span {
        font-size: 20px;
        font-family: Helvetica-Bold, Helvetica;
        font-weight: bold;
        color: #333333;
        vertical-align: middle;
        margin-left: 10px;
      }
      > p {
        font-size: 12px;
        font-family: Helvetica;
        color: #333333;
        text-align: right;
      }
    }
  }
  .endTip {
    opacity: 0.8;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #999999;
    margin-top: 17.5px;
    display: block;
  }
}

.errorMsg {
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #ff8e18;
  background: url(../../assets/images/failed.svg) left no-repeat;
  background-size: 13px;
  padding-left: 18px;
  display: inline-block;
  margin: 0 auto 10px;
}
