@mixin greyText {
  font-family: AvenirNext-Regular;
  font-size: 12px;
  color: #999;
  text-align: left;
}

.v3-order-modal {
  .ant-modal-content {
    border-radius: 20px;
  }
  .ant-modal-footer {
    border: none;
    text-align: center;
  }
}
.v3-order-modal-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  line-height: 1em;
  padding: 10px 0;
  color: #333;
}
.v3-order {
  &-title {
    font-size: 12px;
  }
  &-grey-text {
    @include greyText;
    padding: 20px 0 10px;
    border-bottom: 1px solid rgba(216, 216, 216, 0.5);
    margin-bottom: 15px;
  }
  &-info {
    font-family: AvenirNext-DemiBold;
    font-size: 16px;
    color: #5915e1;
    font-weight: bold;
    margin-top: 7px;
  }
  &-logo {
    width: 16px;
    height: 16px;
    margin-right: 10px;
    vertical-align: middle;
  }
  &-ver-mid {
    vertical-align: middle;
    color: #666;
  }
}
//v3-order
.v3-order {
  &-success {
    text-align: center;
  }
  &-success-icon {
    width: 80px;
    margin-top: 20px;
  }
  &-title-green {
    font-size: 20px;
    color: #16c378;
    margin: 24px 0 12px;
  }
  &-flex {
    display: flex;
    justify-content: center;
    align-items: flex-end;
  }
  &-add-value {
    font-size: 20px;
    color: #333333;
    letter-spacing: 0;
    text-align: right;
    display: inline-block;
    word-break: break-all;
  }
  &-add-token {
    font-size: 12px;
    color: #999;
    margin-top: 9px;
    margin-left: 3px;
  }
  &-plus {
    font-size: 14px;
    color: #5915e1;
    margin: 0 3px;
  }
  &-supplyDescTitle {
    margin-top: 14px;
    opacity: 0.8;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #999999;
  }
}