$blue: #5915e1;
$black: #333333, #666666, #999999;
$gray: #fafafa, #dbdbdb;
$orange: #ff8e18;
$green: #16c378;
$red: #e85d54;
$hoverBlue: #4711b4;
$hoverOrgange: #cc7113;
$superPurple: #5816e0;
$font333: #333;
$font666: #666;
$font999: #999;
$bgColor: #140736;
$white: #ffffff;

.v3-add-page {
  .swap-block {
    background: #444454;
    border-radius: 10px;
    box-shadow: 0 10px 40px 0 rgba(21, 55, 156, 0.06);
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;

    .lock-block {
      width: 100%;
      padding: 0 10px;
      height: 100%;
      background: #444454;
      border-radius: 10px;
      position: absolute;
      z-index: 3;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      > span {
        color: $white;
        font-size: 15px;
        opacity: 0.5;
        margin-top: 8px;
      }
    }

    .left-block {
      font-family: AvenirNext-Regular;
      font-size: 14px;
      color: $font666;
      width: 268px;
      border-radius: 20px;
      padding: 15px 0 15px 15px;

      .input-number {
        input {
          width: 100%;
          border: none !important;
          font-family: AvenirNext-DemiBold;
          font-size: 20px;
          color: $white !important;
          background-color: transparent !important;
        }

        input:focus {
          outline: none;
        }
      }
    }

    .right-block {
      padding: 0 15px 0 0;
      font-size: 15px;

      text-align: right;

      .dragDown {
        background-position: 100% 50%;
        display: inline-block;
        width: auto;
        height: auto;
        padding-right: 25px;
        margin: 10px auto 0;
        background: url(../../assets/images/add/arrow-white-down.png) right no-repeat;
        cursor: pointer;

        span {
          position: relative;
          top: 1px;
          line-height: 21px;
          font-family: AvenirNext-Medium;
          font-size: 20px;
          color: $white;
          font-weight: bold;
        }
      }

      .choose-token {
        background-color: #5a14e1;
        border-radius: 10px;
        background-position: bottom 50% right 10px;
        padding: 10px 25px 10px 10px;
        color: $white;

        .choose-token-text {
          span {
            width: 10px;
            height: 10px;
            background-position: 100% 50%;
            background: url(../../assets/images/add/arrow-white-down.png) right no-repeat;
            vertical-align: baseline;
            margin-left: 20px;
          }
        }
      }

      .disable-choose {
        background: none;
        padding-right: 0;
      }
    }
    .text-op-3 {
      color: $white;
      opacity: 0.3;
    }

    .ml-6 {
      margin-right: 6px;
    }

    .text-op-5 {
      color: $white;
      opacity: 0.5;
    }
    .max-link-btn,
    .min-link-btn {
      background: $orange;
      border-radius: 9px;
      height: 20px;
      line-height: 20px;
      color: $white;
      font-family: AvenirNext-Medium;
      margin-top: 13px;
      font-size: 12px;
      margin-right: 10px;
      min-width: 40px;
      text-align: center;
      width: 45px;
      display: inline-block;

      &:hover {
        // background: $hoverOrgange;
        color: $white;
      }
      &:visited {
        // background: $hoverOrgange;
        color: $white;
      }
      &:link {
        // background: $hoverOrgange;
        color: $white;
      }
    }
    .mobile-swap-top {
      padding: 15px 15px 0 15px;
      .link {
        margin: 0;
      }
      &-input-text {
        color: rgba(255, 255, 255, 0.3);
      }
    }
  }
}

.flex {
  display: flex;
}
.mobile-content {
  .v3-add-page .swap-block {
    .right-block .dragDown {
      white-space: nowrap;
      padding-right: 20px;
      img {
        width: 18px;
        height: 18px;
      }
      span {
        font-size: 16px;
      }
    }
    .top-block .right-block {
      white-space: nowrap;
    }
  }
}

.v3-add-page .swap-block .right-block .max-link-disable {
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.5);
  cursor: default;
  &:hover {
    background: rgba(255, 255, 255, 0.1);
    color: rgba(255, 255, 255, 0.5);
    cursor: default;
  }
}

@media screen and (max-width: 1000px) {
  .v3-add-page {
    .v3-swap-block .v3-add-page-module {
      width: 50%;
    }
    .swap-block .left-block {
      width: auto;
    }
  }
}
.currency-dropdown-input {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
