$white: #ffffff;
$purple: #c1a0ff;

#swap-tab .v3-pools-page {
  .position-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &-logo {
      position: relative;
      img {
        width: 20px;
        height: 20px;

        &:nth-child(2) {
          position: absolute;
          left: 14px;
          top: 3px;
        }
      }
    }
    &-info {
      margin-left: 25px;
      &-top {
        font-weight: bold;
        color: $white;
        font-size: 20px;

        span {
          font-size: 12px;
          opacity: 0.3;
          margin-left: 10px;
        }
      }
      &-down {
        font-size: 12px;
        > a {
          color: $purple;
        }
      }
    }
    &-btn {
      > button {
        border-radius: 6px;
        width: 80px;
        /*  */
        height: 30px;
        font-size: 12px;
        &:nth-child(1) {
          color: #5a14e1;
          background: $purple;
          margin-right: 20px;
        }

        &:nth-child(2) {
          color: $white;
          background: #5a14e1;
        }
      }
    }
    &-mobile-top {
      display: flex;
      align-items: center;
    }
    &-mobile-bottom {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 10px;
      .position-header-btn {
        display: flex;
      }
    }
    &-jump-icon {
      width: 14px;
      height: 14px;
      margin-left: 10px;
    }
  }
  .position-liquidity {
    &-top {
      display: flex;
      justify-content: space-between;
    }
    &-bottom {
      font-size: 15px;
      color: $white;
      text-align: left;
    }
    &-symbol {
      font-weight: bold;
      color: $white;
      img {
        margin-right: 2px;
      }
    }
    &-radio {
      background-color: rgba(255, 255, 255, 0.1);
      color: $white;
      opacity: 0.48;
      font-size: 12px;
      padding: 2px 8px;
    }
  }

  .position-rewards {
    &-content {
      display: flex;
      background: #444454;
      border-radius: 10px;
      padding: 12px 0;
    }
    &-box {
      width: 50%;
      padding: 0 30px;
      &:nth-child(1) {
        border-right: solid 1px rgba(255, 255, 255, 0.1);
      }
      img {
        width: 15px;
        height: 15px;
        vertical-align: sub;
      }
    }
  }

  .position-price {
    &-current {
      display: flex;
      justify-content: space-between;
      margin: 10px 0;
      &-left {
        color: $white;
        &:nth-child(1) {
          opacity: 0.3;
        }
        &:nth-child(2) {
          opacity: 0.7;
          font-weight: bold;
        }
      }
    }
    &-symbol {
      font-weight: bold;
      color: $white;
      opacity: 0.3;
    }
    &-top {
      display: flex;
      justify-content: space-between;
    }
    &-bottom {
      font-size: 20px;
      font-weight: bold;
      color: $white;
      text-align: center;
    }
  }

  // common
  .position-info-text {
    font-size: 15px;
    color: $white;
    opacity: 0.5;
    margin-top: 26px;
  }
  .position-info-content {
    display: flex;
    justify-content: space-between;
    font-size: 20px;
    font-weight: bold;
    color: $white;
    margin: 10px 0;
    > button {
      width: 66px;
      height: 100%;
      border-radius: 4px;
      padding: 5px 10px;
      font-size: 12px;
    }
  }
  .position-info-box {
    background: #444454;
    border-radius: 10px;
    padding: 10px;
    width: 50%;
    text-align: center;

    &:nth-child(2) {
      margin-left: 10px;
    }

    img {
      width: 15px;
      height: 15px;
      vertical-align: sub;
    }
  }

  .ant-skeleton:nth-child(1) {
    margin-bottom: 30px;
    li {
      height: 36px;
    }
  }
  .ant-skeleton:nth-child(2),
  .ant-skeleton:nth-child(3),
  .ant-skeleton:nth-child(6),
  .ant-skeleton:nth-child(7),
  .ant-skeleton:nth-child(9),
  .ant-skeleton:nth-child(10) {
    margin-bottom: 10px;
    li {
      height: 20px;
    }
  }
  .ant-skeleton:nth-child(4),
  .ant-skeleton:nth-child(5),
  .ant-skeleton:nth-child(11),
  .ant-skeleton:nth-child(12) {
    width: calc(50% - 5px);
    margin-bottom: 30px;
    span {
      width: 100%;
      height: 70px;
      border-radius: 10px;
    }
  }
  .ant-skeleton:nth-child(8) {
    margin-bottom: 30px;
    li {
      height: 70px;
    }
  }
}
